* {
    box-sizing: border-box;
}

html,
input,
textarea {
    color: inherit;
}

body {
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

input,
button,
textarea,
select {
    outline: none;
}

button {
    padding: 8px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    background-color: $clr3;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}
